import {Card, DatePicker, ChoiceList} from '@shopify/polaris';
import {useCallback, useState, useEffect}  from 'react';

function Choose(props){
  const [selected, setSelected] = useState(['always']);

  const handleChange = useCallback((value) => setSelected(value), []);

  const renderDatePicker = useCallback(
    (isSelected) =>
      isSelected && (
        <Schedule />
      )
  )

  return (
    <ChoiceList
      choices={[
        {label: 'Always display', value: 'always'},
        {label: 'Only display within the giving period of time', value: 'scheduled',renderChildren:renderDatePicker},
      ]}
      selected={selected}
      onChange={handleChange}
    />
  );
};

function SelectTime(props){
  return (
    <div>
      <div>Please enter the hour(from 0 to 23) and minute(from 0 to 59) that the display <strong>starts</strong>:</div>
      <div>
        <span></span>
      </div>
      <input id="selectHour" type='number' min={0} max={23}/>
      <div>Please enter the hour(from 0 to 23) and minute(from 0 to 59) that the display <strong>ends</strong>:</div>
      <input id="selectHour" type='number' min={0} max={59}/>
    </div>
  )
}

function Schedule(props){
  const [{ month, year }, setDate] = useState({
    month: 9,
    year: 2019
  });
  const [selectedDates, setSelectedDates] = useState({
    start: new Date(),
    end: new Date()
  });
  const [{leftMon,rightMon},setMonth] = useState({
    leftMon:month+1,
    rightMon:month+2
  })

  const handleMonthChange = useCallback((month, year) => {
    setDate({ month, year });
    setMonth({leftMon:month+1,rightMon:month+2})
  }, []);

  const transpileMonth = (month) => {
    month = ''+month;
    switch(month){
      case '1':
        return 'January';
        break;
      case '2':
        return 'Feburary';
        break;
      case '3':
        return 'March';
        break;
      case '4':
        return 'April';
      case '5':
        return 'May';
      case '6':
        return 'June';
      case '7':
        return 'July';
      case '8':
        return 'August';
      case '9':
        return 'September';
      case '10':
        return 'October';
      case '11':
        return 'November';
      case '12':
        return 'December';
      case '13':
        return 'January';
    }
  }

  useEffect(()=>{
    console.log('selected dates are',selectedDates)
  },[selectedDates])


  return (
    <div>
      <div style={{display:'flex',flexFlow:'row nowrap',justifyContent:'stretch'}}>
        <div style={{width:'50%',textAlign:'center'}}>{transpileMonth(leftMon)}</div>
        <div style={{width:'50%',textAlign:'center'}}>{transpileMonth(rightMon)}</div>
      </div>
      <DatePicker 
      month={month} 
      year={year} 
      onChange={setSelectedDates} 
      onMonthChange={handleMonthChange} 
      selected={selectedDates} 
      multiMonth={true} 
      allowRange={true} 
      />
      <SelectTime />
    </div>
  );
}

class DisplaySchedule extends React.Component{
  constructor(props){
    super(props);
    this.state={}
  }

  render(){
    return (
      <Card.Section>
        <div style={{marginBottom:'1em',fontSize:'1.1em'}}>Display schedule:</div>
        <Choose />
        <div>Define the Start Time and End Time of the display period.</div>
      </Card.Section>
    )
  }
}

export default DisplaySchedule;